html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

:root {
    --basic-black: #333333;
    --basic-white: #ffffff;
    --basic-brown: #766357;
    --basic-grey: #f2f2f2;
    --basic-blue: #81b3d2;
    --dark-blue: #669ec0;
    --steel-blue: #6e95bb;
    --dark-grey: #ababab;
    --medium-grey: #a9a9a9;
    --dark-brown: #604e43;
    --chocolate-brown: #503e33;
    --sky-blue: #5496bd;
    --concrete-grey: #7d7d7d;
    --light-grey: #eeeeee;
    --grey-92: #ebebeb;
    --silver: #cacaca;
    --grey-90: #e5e5e5;
    --pale-slate: #bdbbbc;
    --transparent: rgba(255, 255, 255, 0.3);
    --transparent-white: rgba(254, 254, 254, 0.9);
    --transparent-black: rgba(0, 0, 0, 0.3);
    --form-box-shadow: rgba(0, 1, 1, 0.15);
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip: rect(0 0 0 0);
    overflow: hidden;
}

body {
    margin: 0;
    padding: 0 20px;
    font-family: "PT Sans", "Arial", sans-serif;
    text-transform: uppercase;
    background-color: var(--basic-grey);
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: var(--basic-black);
}

/* header */

.header {
    position: relative;
    background-color: var(--basic-white);
}

.page-header {
    box-sizing: border-box;
    z-index: 99;
    max-width: 1200px;
    min-width: 320px;
    margin: 0 auto;
    background-color: var(--basic-white);
}

.main-header {
    position: relative;
    margin: 0;
    font-size: 0;
    padding-right: 0;
    padding-left: 0;
}

img {
    max-width: 100%;
    height: 100%;
