}

.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    background-color: var(--basic-white);
}

.main-navigation {
    font-size: 14px;
    font-weight: bold;
    line-height: 26px;
    color: var(--basic-black);
    background-color: var(--basic-white);
