}

.site-navigation {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    width: 100%;
    min-height: 56px;
    align-items: center;
    padding-left: 72px;
    padding-right: 72px;
}

.site-navigation li {
    width: 228px;
}

.site-navigation li:nth-child(4n+3) {
    margin-left: auto;
}

.site-navigation li:nth-child(4n+3),
.site-navigation li:nth-child(4n+4) {
    text-align: right;
}

.main-header-logo {
    width: 138px;
    height: 70px;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
}

.site-navigation a:hover {
    color: var(--basic-blue);
}

.site-navigation a:focus {
    color: var(--basic-black);
}

.site-navigation a:active {
