/* SEARCH MODAL WINDOW */
.map {
    position: relative;
    height: 593px;
    overflow: hidden;
    z-index: 1;
}

.map-image {
    z-index: 1;
    height: 747px;
    width: 1200px;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
}

.map-interactive iframe {
    position: relative;
    z-index: 2;
    border: none;
}

.search-hotel {
    position: relative;
    z-index: 20;
    margin: 0 auto;
    padding-top: 30px;
    width: 568px;
    text-align: center;
}

.visually-hidden {
    position: absolute;
    overflow: hidden;
    margin: -1px;
    border: 0;
    padding: 0;
    width: 1px;
    height: 1px;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    white-space: nowrap;
}

.search-hotel-title {
    display: block;
    margin-top: 21px;
    margin-bottom: 25px;
    font-size: 30px;
    font-weight: 700;
    color: var(--basic-black);
    line-height: 36px;
}

.search-hotel-text {
    line-height: 24px;
    margin-bottom: 46px;
}

.button {
    display: block;
    border: 0;
    font: inherit;
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    color: var(--basic-white);
    word-wrap: break-word;
    text-transform: inherit;
    cursor: pointer;
}

.button--brown {
    background-color: var(--basic-brown);
    outline: none;
    position: relative;
    padding: 30px 10px;
    width: 100%;
}

.button--brown:hover,
.button--brown:focus {
    background-color: var(--dark-brown);
}

.button--brown:focus {
    background-color: var(--chocolate-brown);
    outline-offset: -3px;
    color: var(--transparent);
}

.interactive-form {
    display: block;
    -webkit-animation: bounce 1.5s;
    animation: bounce 1.5s;
    position: relative;
    z-index: 4;
}

.form-hide {
    display: none;
}

/* Animations */

.form {
    position: absolute;
    z-index: 4;
    transform-origin: top;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--basic-white);
    padding-top: 53px;
    padding-right: 53px;
    padding-bottom: 55px;
    padding-left: 57px;
    box-shadow: 0 7px 15px  var(--form-box-shadow);
}

@-webkit-keyframes bounce {
    0% {
        transform: translateY(-70px);
    }

    30% {
        transform: translateY(10px);
    }

    60% {
        transform: translateY(30px);
    }

    90% {
        transform: translateY(-5px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes bounce {
    0% {
        transform: translateY(-70px);
    }

    30% {
        transform: translateY(10px);
    }

    60% {
        transform: translateY(30px);
    }

    90% {
        transform: translateY(-5px);
    }

    100% {
        transform: translateY(0);
    }
}

@-webkit-keyframes shake {
    0%,
    100% {
        transform: translateX(0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
        transform: translateX(-10px);
    }

    20%,
    40%,
    60%,
    80% {
        transform: translateX(10px);
    }
}

@keyframes shake {
    0%,
    100% {
        transform: translateX(0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
        transform: translateX(-10px);
    }

    20%,
    40%,
    60%,
    80% {
        transform: translateX(10px);
    }
}

.form-error {
    -webkit-animation: shake 0.6s;
    animation: shake 0.6s;
}

