.form-search-button {
    position: relative;
    display: block;
    border: 0;
    width: 38px;
    height: 38px;
    font: inherit;
    background-color: var(--basic-grey);
    cursor: pointer;
    outline-color: var(--basic-grey);
}

.form-search-button::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: var(--medium-grey);
}

.form-search-button::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: var(--medium-grey);
}

.form-search-button:hover::after,
.form-search-button:hover::before {
    background-color: var(--basic-black);
}

.form-search-button:active::after,
.form-search-button:active::before {
    background-color: var(--basic-blue);
}

.form-search-button--minus::before {
    transform: translate(-50%, -50%);
    width: 12px;
    height: 3px;
}

.form-search-button--minus::before {
    transform: translate(-50%, -50%);
    width: 12px;
    height: 3px;
}

.form-search-input-number {
    padding: 5px;
    width: 38px;
