}

.form-search-button--plus::before {
    width: 11px;
    height: 3px;
    transform: translate(-50%, -50%);
}

.form-search-button--plus::after {
    transform: translate(-50%, -50%) rotate(90deg);
    width: 11px;
    height: 3px;
}

.form-search-label--children {
