/* ФИЛЬТРЫ СТРАНИЦА ПОИСКА ГОСТИНИЦ */

.form-filters {
    display: flex;
    padding: 27px 6% 6px;
    color: var(--basic-white);
    background-color: var(--steel-blue);
    background-image: url(../img/sedona-canyon-blur.jpg);
    background-position: 50% 16%;
    background-size: cover;
}

.form-filters-wrapper-left {
    display: flex;
    width: 48%;
}

.form-filters-fieldset {
    flex-grow: 1;
    margin: 0;
    padding: 0;
    border: 0;
}

.form-filters-legend {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    padding: 0;
}

.form-filters-input-wrapper {
    margin: 0;
    padding-left: 0;
    list-style: none;
}

.form-filters-input-block {
    margin: 25px 0;
}

.form-filters-label-checkbox {
    position: relative;
    padding-left: 40px;
    cursor: pointer;
}

.form-filters-label-checkbox::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 23px;
    height: 23px;
    background-image: url(../img/checkbox-off.svg);
    background-repeat: no-repeat;
}

.form-filters-checkbox:checked + .form-filters-label-checkbox::before {
    width: 27px;
    background-image: url(../img/checkbox-on.svg);
}

.form-filters-checkbox:focus + label::before {
    content: "";
    position: absolute;
    background-image: url('../img/checkbox-off.svg');
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 23px;
    height: 23px;
}

.form-filters-checkbox:disabled + label::before {
    content: "";
    position: absolute;
    width: 23px;
    height: 23px;
    background-image: url("../img/checkbox-off-inactive.svg");
    background-repeat: no-repeat;
    left: -1px;
    top: 1px;
}

.form-filters-checkbox:disabled:checked + label::after {
    content: "";
    position: absolute;
    background-image: url('../img/checkbox-off.svg');
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 27px;
    height: 23px;
}

.form-filters-checkbox:checked:disabled + label::after {
    content: "";
    position: absolute;
    width: 27px;
    height: 23px;
    background-image: url("../img/checkbox-on-inactive.svg");
    background-repeat: no-repeat;
    left: -1px;
    top: 1px;
}

.form-filters-checkbox:checked:disabled + label::before {
    opacity: 0;
}

.form-filters-wrapper-right {
    margin-left: auto;
    width: 30%;
}

.form-filters-legend {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
}

.form-filters-label {
    display: block;
    width: 40%;
    text-align: right;
}

.zero {
    padding-right: 10px;
}

.three-thousand {
    padding-left: 10px;
}

.form-filters-interval {
    position: relative;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 10px;
    margin-bottom: 0;
    padding: 5px 20px;
    border: 2px solid var(--basic-white);
    border-radius: 2px;
}

.form-filters-interval::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 2px;
    height: 22px;
    transform: translate(-50%, -50%);
    background-color: var(--basic-white);
}

.form-filters-range {
    position: relative;
    margin: 21px 0 32px;
}

.form-filters-range-scale {
    height: 2px;
    background-color: var(--transparent);
}

.form-filters-range-bar {
    width: 80%;
    height: 2px;
    background-color: var(--basic-white);
}

.form-filters-input-text {
    box-sizing: border-box;
    padding-left: 1px;
    padding-right: 2px;
    max-width: 49%;
    border: 0;
    font: inherit;
    color: inherit;
    background: 0;
}

.form-filters-range-handle {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    box-sizing: content-box;
    padding: 0;
    width: 4px;
    height: 4px;
    border: 9px solid var(--basic-white);
    border-radius: 50%;
    background-color: var(--dark-grey);
    cursor: pointer;
}

.form-filters-range-handle:hover {
    border-width: 10px;
}

.form-filters-range-handle--min {
    left: 0;
}

.form-filters-range-handle--max {
    left: 79%;
}

.form-filters-button {
    display: block;
    margin-left: 85px;
    padding: 5px 34px;
    border: 2px solid var(--basic-white);
    border-radius: 2px;
    background: 0;
    font: inherit;
    color: inherit;
    text-transform: inherit;
    cursor: pointer;
}

.form-filters-button:hover {
    display: block;
    margin: 0 auto;
    padding: 5px 34px;
    border: 2px solid var(--basic-white);
    border-radius: 2px;
    font: inherit;
    font-style: normal;
    color: var(--basic-black);
    text-transform: inherit;
    cursor: pointer;
    background-color: var(--basic-white);
}

/* сОРТИРОВКА */

.catalog-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 23px 6% 21px;
    line-height: 26px;
    border-bottom: 1px solid var(--grey-90);
}

.catalog-result-wrapper {
    width: 134px;
}

.catalog-result {
    font-size: 21px;
    font-weight: 700;
}

.catalog-sort-type-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-grow: 1;
    padding-left: 30px;
    padding-right: 10px;
    font-size: 12px;
    line-height: 18px;
}

.catalog-sort-text {
    width: 105px;
}

.sort-type {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding-left: 10px;
    list-style: none;
}

.sort-type-item {
    padding-right: 33px;
}

.sort-type-link {
    color: var(--transparent-black);
    border-bottom: 1px dotted var(--basic-blue);
}

.sort-type-link--active {
    border-bottom: 0;
    color: var(--basic-blue);
}

.sort-type-link:hover {
    color: var(--basic-blue);
    border-bottom: 1px dotted var(--basic-blue);
}

.sort-type-link:active {
    color: var(--basic-black);
    border-bottom: none;
}

a {
    background-color: transparent;
    text-decoration: none;
    color: inherit;
}

.sort-direction {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 0;
    padding-left: 0;
    list-style: none;
    width: 110px;
}

.sort-direction-item {
    padding-left: 13px;
}

.sort-direction-link {
    display: block;
    width: 0;
    height: 0;
    border-left: 5.5px solid transparent;
    border-right: 5.5px solid transparent;
    border-bottom: 10px solid var(--silver);
}

.sort-direction-link:active {
    display: block;
    width: 0;
    height: 0;
    border-left: 5.5px solid transparent;
    border-right: 5.5px solid transparent;
    border-bottom: 10px solid var(--basic-blue);
}

.sort-direction-link:hover {
    display: block;
    width: 0;
    height: 0;
    border-left: 5.5px solid transparent;
    border-right: 5.5px solid transparent;
    border-bottom: 10px solid var(--basic-black);
}

.sort-direction-link:focus {
    display: block;
    width: 0;
    height: 0;
    border-left: 5.5px solid transparent;
    border-right: 5.5px solid transparent;
    border-bottom: 10px solid var(--basic-blue);
}

.sort-direction-link--active {
    border-bottom-color: var(--basic-blue);
}

.sort-direction-link--descending {
    transform: rotate(180deg);
}

/* РЕЗУЛЬТАТЫ ПОИСКА */

.hotel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 6% 29px;
    border-bottom: 1px solid var(--grey-90);
}

.hotel-image-wrapper {
    margin: 0;
    padding: 0;
    width: 134px;
}

.hotel-image {
    display: block;
    width: 135px;
    height: 90px;
}

.hotel-description-wrapper {
    flex-grow: 1;
    padding-left: 31px;
    padding-right: 31px;
}

.hotel-name {
    margin-top: 0;
    margin-bottom: 8px;
    font-size: 21px;
    line-height: 26px;
}

.hotel-link {
    color: var(--basic-black);
}

.hotel-link:hover {
    color: var(--basic-blue);
}

.hotel-link:active {
    color: var(--basic-black);
    opacity: 30%;
}

.hotel-description {
    display: flex;
}

.hotel-info {
    margin-right: 6px;
    width: 110px;
}

.hotel-text {
    padding-right: 50px;
    margin-top: 0;
    margin-bottom: 14px;
    text-align: center;
}

.hotel-button {
    padding: 3px 16px;
    font-size: 14px;
    line-height: 21px;
    white-space: nowrap;
}

.button--blue {
    background-color: var(--basic-blue);
}

.button--blue:hover {
    background-color: var(--dark-blue);
}

.button--blue:focus {
    background-color: var(--dark-blue);
}

.button--blue:active path {
    background-color: var(--dark-blue);
    color: var(--transparent);
    outline: none;
}

.button--brown {
    background-color: var(--basic-brown);
}

.button--brown:hover {
    background-color: var(--dark-brown);
}

.button--brown:active {
    background-color: var(--chocolate-brown);
    color: var(--transparent);
}

.button {
    border: 0;
    font-weight: 700;
    color: var(--basic-white);
    text-transform: inherit;
    cursor: pointer;
}

.hotel-booking {
    width: 142px;
}

.hotel-rating {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    align-self: flex-start;
    padding-top: 4px;
    width: 110px;
}

.hotel-stars {
    max-width: 110px;
    height: 17px;
    background-image: url(../img/star.svg);
    background-repeat: space;
}

.hotel-stars--two {
    width: 41px;
}

.hotel-stars--three {
    width: 65px;
}

.hotel-stars--four {
    width: 89px;
}

.hotel-rating-text {
    margin-top: 47px;
    margin-bottom: 0;
    padding: 3px 13px;
    background-color: var(--basic-grey);
}
